<script>
	import { onDestroy, onMount } from "svelte";

	import {
		handleStackRouterComponentMount,
		handleStackRouterComponentDestroy,
	} from "./stack-router";

	let mountPoint;
	onMount(() => {
		handleStackRouterComponentMount(mountPoint);
	});
	onDestroy(() => {
		handleStackRouterComponentDestroy()
	});
</script>

<style>
	.stack-router-mount-point {
		position: relative;
	}
</style>

<div class="stack-router-mount-point" bind:this={mountPoint}></div>